<template>
  <div class="main">
    <div class="crumb_box">
      <div class="search_box">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item>首页</el-breadcrumb-item>
          <el-breadcrumb-item>组织列表</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
    </div>
    <div class="clear"></div>
    <div class="application_item">
      <div class="btn_box">
        <el-button type="primary" @click="stepadd">创建组织</el-button><el-button  type="primary"  @click="visible.joinGroupList = true" style="margin-left: 10px">加入组织</el-button>
        <span  style="margin-left: 10px"> <i class="icon iconfont icontishi" style="color: #f4a100"></i>&nbsp;您可以申请加入其他组织或者创建新组织，实现跨团队协作！</span>
      </div>
      <div class="clear"></div>
      <div class="table_item">
        <el-table ref="multipleTable" stripe :data="groupLists" tooltip-effect="dark" height="calc(100vh - 205px)" style="width: 100%;border-bottom: none">
          <el-table-column type="index" width="55" label="序号"></el-table-column>
          <el-table-column prop="company" label="组织名称" width="200" :show-overflow-tooltip="tableConfig.overflow"></el-table-column>
          <el-table-column label="申请日期" width="130"  :show-overflow-tooltip="tableConfig.overflow">
            <template slot-scope="scope">
              <span v-show="scope.row.applyDate != null">{{dateFrm(scope.row.applyDate)}}</span>
              <span v-show="scope.row.applyDate == null" style="color: #999">暂无</span>
            </template>
          </el-table-column>
          <el-table-column label="状态" width="55" :show-overflow-tooltip="tableConfig.overflow" >
            <template slot-scope="scope">
              <span v-show="scope.row.applyStatus == 1">离职</span>
              <span v-show="scope.row.applyStatus == 0">在职</span>
              <span v-show="scope.row.applyStatus == null" style="color: #999">暂无</span>
            </template>
          </el-table-column>
          <el-table-column> </el-table-column>
          <el-table-column label="操作" width="190">
            <template slot-scope="scope" class="btn_txt" >
            <el-button type="text" @click="quitCompanys(scope.row.dbcOneId)">退出组织</el-button><b>|</b>
            <el-button type="text" @click="workAdd">工作交接</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>

    <!--加入组织-->
    <el-dialog class="private-pop" title="加入组织" :visible.sync="visible.joinGroupList" width="600px">
      <div style="margin-bottom: 50px">
        <el-input style="float: left;width: 100%"  @change="getallGroupLists"   v-model="search.keyword"   placeholder="搜索两个字以上的组织关键字..." suffix-icon="el-icon-search"></el-input>
      </div>
      <div >
        <el-table :data="dataList" style="width: 100%;border-bottom: none" height="289" >
          <el-table-column type="index" width="55" label="序号"></el-table-column>
          <el-table-column prop="company" label="组织名称" width="200" :show-overflow-tooltip="tableConfig.overflow"></el-table-column>
          <el-table-column prop="userName" label="组织联系人" width="100" :show-overflow-tooltip="tableConfig.overflow"></el-table-column>
          <el-table-column  label="操作" >
            <template slot-scope="scope">
              <el-button @click="joinGroupSubmit(scope.row.dbcId)" type="text" size="small">申请加入</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="pagination" style="padding-bottom: 20px">
        <el-pagination
          v-if='paginations.total > 0'
          :page-sizes="paginations.page_sizes"
          :page-size="paginations.page_size"
          :layout="paginations.layout"
          :total="paginations.total"
          :current-page.sync='paginations.page_index'
          @current-change='handleCurrentChange'
          @size-change='handleSizeChange'>
        </el-pagination>
      </div>

      <span slot="footer" class="dialog-footer">
          <div style="padding: 5px 0px;height: 36px">
            <i class="icon iconfont icontishi" style="float: left;color: #f4a100;display: inline-block"></i>&nbsp;
            <span style="float: right;display: inline-block;width: 540px;text-align: left">提示：以上是与您搜索相类似的组织，您可以输入更接近的关键字，也可以申请加入组织、
              <span style="color: #00aaee; cursor: pointer" @click="stepadd">创建组织</span></span>
          </div>
       </span>
    </el-dialog>

    <!--创建组织-->
    <el-dialog title="创建组织" :visible.sync="visible.addGroupList"  width="600px" class="uploadadress">
      <el-form :model="addGroupForm"   ref="addGroupForm_rules"  label-width="93px" class="demo-ruleForm">
        <el-form-item label="联系电话">
          <el-input v-model="users.mobile"  disabled></el-input>
        </el-form-item>
          <el-form-item label="组织名称：" prop="companyName" :rules="[ { required: true, message: '组织名称为空', trigger: 'blur' }]">
            <el-input v-model="addGroupForm.companyName"  placeholder="请输入组织名称"></el-input>
          </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="addGroupSubmit">创 建</el-button>
        <el-button @click="visible.addGroupList = false">取 消</el-button>
      </div>
      <div class="clear"></div>
    </el-dialog>
  </div>
</template>

<script>
  import {getCompanyList,quitCompany,createCompany,joinCompany,getOrganizeList} from '@/api/UserApi';
  import moment from 'moment'
  export default {
    name: "GroupList",
    data() {
      return {
        visible:{
          joinGroupList:false,
          addGroupList:false,
        },
        search: {
          keyword: '',
        },
        tableConfig: {
          loading: false,
          overflow: true,
        },
        radio:0,
        addGroupForm:{
          companyName:""
        },
        dataList: [
          // {
          //   index:1,
          //   company:"浙江中之杰智能系统有限公司",
          //   userName:"陈川",
          // },
        ],
        pagination: {
          page: 1,
          sizes: 20,
          total: 0,
        },
        groupLists:[
          // {
          //   dbcOneId:61008,
          //   companyId:153,
          //   company:"浙江中之杰智能系统有限公司",
          //   userId:"O9CPFPVO",
          //   userName:"陈川",
          //   applyDate:null,
          //   applyStatus:null
          // },
        ],
        users:{
          // departId:"",
          // departName:"",
          // departPath: "",
          // email: "",
          // entryTime: null,
          // image: null,
          // jobNumber: null,
          // jobStatus: 0,
          // mobile: "",
          // registerTime: null,
          // sex: null,
          // userId: "",
          //  userName:"",
          // userPosts:[
          //   {userPostid:""},
          //   {userPostname:""}
          // ]
        },
        //需要给分页组件传的信息
        paginations: {
          page_index: 1, // 当前位于哪页
          total: 0, // 总数
          page_size: 5, // 1页显示多少条
          page_sizes: [ 5, 10, 15], //每页显示多少条
          layout: "total, sizes, prev, pager, next, jumper" // 翻页属性
        },
      }
    },
    created(){
      this.getGroupLists();
      this.getUser();
    },
    methods:{
      //获取用户列表
      getUser: function () {
        this.users=JSON.parse(localStorage.getItem("user-center-token"));
      },
      stepadd(){
        this.addGroupForm.companyName = "";
        this.visible.joinGroupList = false;
        this.visible.addGroupList =  true;
      },
      dateFormat: (row, column)=> {
        const date = row[column.property]
        if (date === undefined) {
          return ''
        }
        //这里的格式根据需求修改
        return moment(date).format('YYYY-MM-DD')
      },
      dateFrm: (e) =>{
        // console.log(e);
        return moment(e).format('YYYY-MM-DD');
      },
      //获取团队信息列表
      getGroupLists: function () {
        getCompanyList().then((res) => {
          if (res.data.code === this.$global.successCode) {
            //console.log("请求团队列表信息数据成功",res);
            this.groupLists = res.data.data;
          }
        }, function (error) {
          console.log("请求团队列表信息数据失败",error);
        });
      },
      //获取所有团队信息列表
      getallGroupLists() {
        getOrganizeList({query: this.search.keyword}).then((res) => {
          if (res.data.code === this.$global.successCode) {
            //console.log("请求数据成功",res);
            this.allTableData = res.data.data;
            this.setPaginations();
          }
        }, function (error) {
          console.log("请求数据失败",error);
        });
      },
      handleCurrentChange(page) {
        // 当前页
        let sortnum = this.paginations.page_size * (page - 1);
        let table = this.allTableData.filter((item, index) => {
          return index >= sortnum;
        });
        // 设置默认分页数据
        this.dataList = table.filter((item, index) => {
          return index < this.paginations.page_size;
        });
      },
      handleSizeChange(page_size) {
        // 切换size
        this.paginations.page_index = 1;
        this.paginations.page_size = page_size;
        this.dataList = this.allTableData.filter((item, index) => {
          return index < page_size;
        });
      },
      setPaginations() {
        // 总页数
        this.paginations.total = this.allTableData.length;
        this.paginations.page_index = 1;
        this.paginations.page_size = 5;
        // 设置默认分页数据
        this.dataList = this.allTableData.filter((item, index) => {
          return index < this.paginations.page_size;
        });

      },

      //退出团队
      quitCompanys(corId){
        //console.log(typeof corId)
        this.$confirm('是否确定退出该团队？', '提示', {confirmButtonText: '确定', cancelButtonText: '取消', type: 'warning'}).then(() => {
          quitCompany({dbcOneId: corId}).then((res) => {
            if (res.data.code === this.$global.successCode) {
              //console.log("确定退出该团队成功",res);
              this.$message({
                message: "退出该团队成功！",
                type: "success"
              });
              this.getGroupLists();
            }
          }, function (error) {
            console.log("退出该团队失败",error);
          });
        }).catch(() => {
        });


      },

      //交接工作
      workAdd(){
        this.$message({
           message: "暂未实现该功能！",
           type: "warning"
        });
      },

      //创建团队
      addGroupSubmit(){
        const form = this.addGroupForm;
        this.$refs['addGroupForm_rules'].validate(valid => {
          if (valid) {
            createCompany(form).then((res) => {
              if (res.data.code === this.$global.successCode) {
                //console.log("添加组织成功",res);
                // 操作成功
                this.$message({
                  message: "添加组织成功！",
                  type: "success"
                });
                this.getGroupLists();
              }
            }, function (error) {
              console.log("添加组织失败",error);
            });

            this.visible.addGroupList = false;
          }
        })

      },

      //加入团队
      joinGroupSubmit(corId){
        //console.log(typeof corId)
        this.$confirm('是否确定加入该组织？', '提示', {confirmButtonText: '确定', cancelButtonText: '取消', type: 'warning'}).then(() => {
          joinCompany({companyID: corId}).then((res) => {
            if (res.data.code === this.$global.successCode) {
              //console.log("确定退出该团队成功",res);
              this.$message({
                message: "加入该组织成功！",
                type: "success"
              });
              this.getGroupLists();
            }
          }, function (error) {
            console.log("加入该组织失败",error);
          });
        }).catch(() => {
        });
       // this.visible.addGroupList = false;
      },

    }
  }
</script>

<style scoped>

</style>




